*{
    margin: 0;
    font-family: Metropolis;
    overflow: hidden;
}
html{
    width: 101%;
}
@font-face {
    font-family: Metropolis;
    font-weight: bold;
    src: url('./Metropolis-Bold.otf');
}
@font-face {
    font-family: Metropolis;
    src: url('./Metropolis-Regular.otf');
}
.messages{
    position: relative;
    z-index: 1;
    width: 17.5vw;
    height: 100vw;
    border-right: 1px solid whitesmoke;
    overflow: hidden;
    float: left;
}
.messagesheader{
    margin-top: 1vw;
    width: 17.5vw;
    height: 2.5vw;
    border-bottom: 1px solid whitesmoke;
}
.messagesheader h1{
    float: left;
    font-size: 1.25vw;
    padding-top: 0.75vw;
}
.profilephoto{
    float: left;
    width: 2vw;
    margin: 0.25vw;
    margin-left: 0.5vw;
    border-radius: 50% 50% 50% 50%;
}
.icon{
    float: right;
    width: 1.5vw;
    margin: 0.5vw;
    transition: ease-in-out 0.2s;
}
.icon:hover{
    cursor: pointer;
    transform: translate(0, -5px);
    transition: ease-in-out 0.2s;
}
.navicon{
    float: left;
    width: 1vw;
    margin-top: 0.75vw;
    margin-left: 1vw;
    margin-right: 1vw;
    transition: ease-in-out 0.2s;
}
.navicon:hover{
    cursor: pointer;
    transform: translate(0, -5px);
    transition: ease-in-out 0.2s;
}
.navbar{
    width: 17.5vw;
    height: 2.5vw;
    background-color: #0397DF;
    position: fixed;
    bottom: 0;
}
.friendtitle{
    position: relative;
    z-index: 0;
    width: 62.5vw;
    height: 3vw;
    border-bottom: 1px solid whitesmoke;
    box-shadow: 2px 3px 8px #00000015;
}
.chat{
    width: 62.5vw;
    height: 100vw;
    float: left;
}
.friendinfo{
    width: 20vw;
    height: 100vw;
    border-left: 1px solid #00000015;
}
.friendinfophoto{
    width: 10vw;
    border-radius: 50%;
    margin-left: 5vw;
    margin-top: 3vw;
}
.locationbutton{
    border-radius: 5%;
    margin: 0 auto;
    margin-top: 1vw;
    width: 15vw;
    height:2vw;
    border: 1px solid whitesmoke;
    box-shadow: 1px 5px 8px #00000015;
}
.locationbutton h1{
    margin: 0.5vw;
    font-size: 0.75vw;
    opacity: 80%;
}
.savedmedia{
    border-radius: 5%;
    margin: 0 auto;
    margin-top: 1vw;
    width: 15vw;
    height:10vw;
    border: 1px solid whitesmoke;
    box-shadow: 1px 5px 8px #00000015;
}
.savedmedia h1{
    font-size: 1vw;
    position: relative;
    margin: 1vw;
    bottom: 0;
}
.typearea{
    width: 62.5vw;
    height: 2.5vw;
    position: absolute;
    bottom: 0;
}
.typeareaicon{
    float: left;
    width: 1vw;
    margin: 0.75vw;
    margin-right: 0.25vw;
    transition: ease-in-out 0.2s;
}
.typeareaicon:hover{
    cursor: pointer;
    transform: translate(0, -5px);
    transition: ease-in-out 0.2s;
}
.typearea textarea{
    resize: none;
    width: 50vw;
    height: 1.25vw;
    margin-top: 0.25vw;
    margin-left: 0.5vw;
    border-radius: 2vw;
    border: none;
    background-color: whitesmoke;
    padding-left: 1vw;
    padding-top: 0.5vw;
    font-size: 1vw;
}
textarea:focus, input:focus{
    outline: 0;
}